import UserItem from '../../../components/UserItem/UserItem.vue'
import UserModel from '@/models/user.js'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
const userModel = new UserModel()
export default {
	data() {
		return {
			userData: {
				// name: '张三'
			},
			user:{},
			dialogActive: 0
		}
	},
	components: {
		UserItem,
		uniPopup
	},
	// onLoad(){
	// 	this.getUserInfo()
	// },
	mounted(){
		this.getUserInfo()
	},
	methods: {
		getUserInfo(){
			userModel.getUserInfo().then(res=>{
				//数据绑定
				const { code, message, data } = res.data
				if(code === '0'){
					this.user = data
					this.userData = data
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.showToast({
					icon: 'none',
					title:'获取用户信息失败'
				})
			})
		},
		switchItem(index){
			this.dialogActive = index
		},
		cashoutList(){
			this.$refs.recommendRecord.open()
			uni.showLoading({
				title: '加载中...'
			})
			userModel.getCashoutList().then(res=>{
				uni.hideLoading()
				const {code, message, data } = res.data
				if(code === '0'){
					this.$refs.recommendRecord.open()
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '获取提现信息失败'
				})
			})
		},
		closeRecommendDialog(){
			this.$refs.recommendRecord.close()
		},
	}
}
